#file-sheet {
  .header {
    display: flex;
    align-items: center;
    padding: 16px;

    &:hover {
      cursor: pointer;
    }

    .label {
      flex: 1;
      color: var(--icon-color);
      font-weight: 500;
      font-size: 13px;
      text-transform: capitalize;
    }

    .icon {
      color: var(--icon-color-inverted);
      transition: all 0.3s ease;
    }
  }

  .no-files {
    color: var(--icon-color);
    text-align: center;
    padding: 0px 16px 16px;
  }
}
</style>
