<template>
  <div id="entity-browse">
    <Portal to="primary-action">
      <BaseButton
        v-if="showUploadFiles"
        icon="eva-upload"
        label="upload files"
        class="q-ml-md"
        @click="uploadFiles"
      />
    </Portal>
    <keep-alive>
      <Portal to="action-bar">
        <ItemActions
