          origin === "http://localhost:8080") &&
          this.repositoryId === "1136") ||
        ((origin === "https://edmsuat.sobhaapps.com" ||
          origin === "http://localhost:8080") &&
          this.repositoryId === "80") ||
        (origin === "https://edms.sobhaapps.com" && this.repositoryId === "80")
