            isSortable: false,
            isGroupable: false,
            dataType: DataType.SHORT_TEXT,
            isFilter: false,
            fieldId: 0,
            addInfo: true,
          });
        }
        additionalInfoColumns.push({
          id: this.$nano.id(),
          name: "status",
          label: "Workflow Status",
          type: "STATUS",
          isVisible: true,
          isSortable: false,
          isGroupable: false,
          dataType: DataType.SHORT_TEXT,
          isFilter: false,
          fieldId: 0,
          addInfo: true,
        });
        additionalInfoColumns.push({
          id: this.$nano.id(),
          name: "revision",
          label: "Revision",
          type: "NORMAL",
          isVisible: true,
          isSortable: false,
          isGroupable: false,
          dataType: DataType.SHORT_TEXT,
          isFilter: false,
          fieldId: 0,
          addInfo: true,
        });
      }

      this.columns = this.columns.filter(
        (field) => field.fieldId === 0 && !field.addInfo
      );
      let listColumns = [];
      this.repositoryDetails.fields.forEach((field) => {
        if (field.visible) {
          listColumns.push({
            id: this.$nano.id(),
            name: field.name,
            label: field.name,
            type: "NORMAL",
