    getFoldersFieldsData(entity) {
      const data = {};
      if (entity.fieldlist) {
        let fieldList = JSON.parse(entity.fieldlist);
        this.repositoryColumns.forEach((field) => {
          let fieldRow = fieldList.filter((row) => row.fieldId === field.name);
          if (fieldRow.length) {
            data[field.name] = fieldRow[0].value;
          }
        });
      }
      return data;
