<script>
import CheckboxField from "@/components/common/form/checkbox-field/CheckboxField.vue";
import Modal from "@/components/common/popup/Modal.vue";
import FileIcon from "@/components/common/FileIcon.vue";

export default {
  name: "ItemList",

  components: {
    CheckboxField,
    Modal,
    FileIcon,
  },

  props: {
    columns: {
      type: Array,
      required: true,
    },

    items: {
      type: Array,
      required: true,
    },

    mode: {
      type: String,
      required: true,
    },

    noBackground: {
      type: Boolean,
      default: false,
    },

    noActions: {
      type: Boolean,
      default: false,
    },

    tableHeader: {
      type: Boolean,
      default: true,
    },

    security: {
      type: Boolean,
      default: true,
    },

    checkbox: {
      type: Boolean,
      default: false,
    },

    hasMetaDataEdit: {
      type: Boolean,
      required: false,
    },

    breadcrumbs: {
      type: Boolean,
      default: false,
    },

    report: {
      type: Boolean,
      default: false,
    },

    resend: {
      type: Boolean,
      default: false,
    },

    selectAll: {
      type: Boolean,
      default: false,
    },

    repository: {
      type: Boolean,
      default: false,
    },

    showExport: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      selectedRowIndex: -1,
      userId: this.$store.state.session.id,
      email: this.$store.state.session.email,
      selectedDummyData: false,
      expandTableModal: false,
      tableTitle: "",
      tableData: [],
    };
  },

  computed: {
    _columns() {
      return this.columns.filter(
        (column) => column.type !== "TITLE" && column.isVisible
      );
    },

    title() {
      return this.columns.find((column) => column.type === "TITLE");
    },

    leftcount() {
      if (this.repository && this.checkbox) {
        return "80px";
      } else {
        return "36px";
      }
    },

    _subWorkflowColumns() {
      let tableColumns = [
        {
          id: this.$nano.id(),
          name: "stage",
          label: "Stage",
          type: "SINGLE_SELECT",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
        },
        {
          id: this.$nano.id(),
          name: "raisedBy",
          label: "Raised By",
          type: "SINGLE_SELECT",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
        },
        {
          id: this.$nano.id(),
          name: "raisedAt",
          label: "Raised On",
          type: "DATE",
          isVisible: true,
          isSortable: true,
          isGroupable: false,
        },
      ];
      return tableColumns;
    },
  },

  created() {
    this.items.forEach((group) => {
      group.data.forEach((item) => {
        this.$set(item, "selected", false);
      });
    });
  },

  methods: {
    visibility(item) {
      const userId = Number(this.userId);
      const email = this.email;
      let hasMatch = false;
      const ownerIds = item.owner ? item.owner.map((owner) => owner.id) : [0];
      const coordinatorIds = item.coordinator
        ? item.coordinator.map((coordinator) => coordinator.id)
        : [0];
      if (
        item.createdBy === email ||
        ownerIds.includes(userId) ||
        coordinatorIds.includes(userId) ||
        userId === 1
      ) {
        hasMatch = true;
      }
      return hasMatch;
    },

    handleCheckboxChange(id) {
      this.selectAll = false;
      this.selectedDummyData = !this.selectedDummyData;
      const itemToUpdate = this.items
        .flatMap((group) => group.data)
        .find((item) => item.id === id);

      if (itemToUpdate) {
        this.$set(itemToUpdate, "selected", itemToUpdate.selected);
      }
      this.$emit("update-selected-all", this.selectAll);
    },

    handleSelectAll() {
      if (this.selectAll === true) {
        this.items.forEach((group) => {
          group.data.forEach((item) => {
            this.$set(item, "selected", true);
          });
        });
      }
      if (this.selectAll === false) {
        this.items.forEach((group) => {
          group.data.forEach((item) => {
            this.$set(item, "selected", false);
          });
        });
      }
      this.$emit("update-selected-all", this.selectAll);
    },

    showTable(name, value) {
      if (value) {
        if (this.report) {
          this.$emit("showTable", name, value);
        } else {
          this.tableTitle = name;
          this.tableData = JSON.parse(value);
          this.expandTableModal = true;
        }
      }
    },

    handleOpenInput(data, column) {
      this.$emit("openMetaDataEdit", data, column);
    },

    viewFile(file, item) {
      file.createdBy = item.createdBy;
      file.createdAt = item.createdAt;
      if (!file.name) {
        file.name = file.fileName;
      }
      if (!file.id) {
        file.id = file.itemId;
      }
      this.$emit("viewFile", file);
    },

    fileType(fileName) {
      if (fileName) return fileName.split(".").reverse()[0].slice(0, 4);
      else "";
    },

    toggleLink(item, data) {
      item.expandSubworkflow = !item.expandSubworkflow;
      item.link ? "" : this.$emit("select", item.id, data);
      this.selectedDummyData = !this.selectedDummyData;
    },

    openSubProcess(item) {
      this.$emit("openSubWorkflow", item);
    },
  },
};
</script>

