<template>
  <Confirm
    :value="value"
    :icon="header.icon || ''"
    icon-color="primary"
    width="450px"
    @input="closeModal"
  >
    <!-- title -->

    <template #title>{{ header?.label }}</template>

    <!-- ... -->

    <template #default>
      <ValidationObserver ref="form">
        <!-- field -->

        <ValidationProvider
          v-slot="{ errors }"
          name="field"
          :rules="{ required: true }"
        >
          <TextField
            v-model="password"
            :label="
              header.id === 'Encrypt'
                ? 'enter a password for to encrypt this file'
                : 'enter a password for to decrypt this file'
            "
            is-mandatory
            :error="errors[0]"
            class="q-my-lg"
          />
        </ValidationProvider>

        <!-- ... -->
      </ValidationObserver>
    </template>

    <!-- footer -->

    <template #footer>
      <BaseButton is-flat label="cancel" class="q-mr-sm" @click="closeModal" />

      <BaseButton label="save" />
    </template>

    <!-- ... -->
  </Confirm>
</template>

<script>
import { ValidationObserver, ValidationProvider } from "vee-validate";
import Confirm from "@/components/common/popup/Confirm.vue";
import TextField from "@/components/common/form/text-field/TextField.vue";

export default {
  name: "EncryptDecrypt",

  components: {
    Confirm,
    ValidationObserver,
    ValidationProvider,
    TextField,
  },

  props: {
    value: {
      type: Boolean,
      default: false,
    },

    header: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      password: "",
    };
  },

  methods: {
    closeModal() {
      this.$emit("close", false);
    },
  },
};
</script>

<style lang="scss" scoped></style>
