<template>
  <ItemDetails
    width="620px"
    :value="value"
    :history="history"
    :overview="overview"
    @edit="handleEdit"
    @input="handleInput"
    @delete="handleDelete"
  >
    <template #title>Folder Details</template>

    <template #fields="{ value: fields }">
      <ol v-if="fields && fields.length">
        <li v-for="field in fields" :key="field.id">
          {{ field.name }}
        </li>
      </ol>

      <div v-else>-</div>
    </template>
  </ItemDetails>
</template>

<script>
import { repository } from "@/api/factory.js";
import ItemDetails from "@/components/common/display/item-details/ItemDetails.vue";

export default {
  name: "RepositoryDetails",

  components: { ItemDetails },

  props: {
    value: {
      type: Boolean,
      default: false,
    },

    columns: {
      type: Array,
      required: true,
    },

    repository: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      isLoading: false,
      history: [],
    };
  },

  computed: {
    overview() {
      return this.columns.map((column) => ({
        id: column.id,
        name: column.name,
        label: column.label,
        value: this.repository[column.name],
      }));
    },
  },

  watch: {
    repository: {
      immediate: true,
      deep: true,
      handler() {
        if (this.repository.id) {
          this.getRepositoryHistory();
        }
      },
    },
  },

  methods: {
    processRepositoryFields(fields) {
      if (this.showAllRepositoryFields) {
        return fields;
      }

      return fields.slice(0, 5);
    },

    handleInput(value) {
      this.$emit("input", value);
    },

    handleEdit() {
      this.$emit("input", false);
      this.$emit("edit", this.repository.id);
    },

    handleDelete() {
      this.$emit("input", false);
      this.$emit("delete", this.repository.id);
    },

    /* api functions */

    async getRepositoryHistory() {
      this.isLoading = true;

      const { error, payload } = await repository.getRepositoryHistory(
        this.repository.id
      );

      this.isLoading = false;

      if (error) {
        this.$alert.error(error);
        return;
      }

      this.history = payload;
    },

    /* ... */
  },
};
</script>

<style lang="scss" scoped>
ol {
  padding-left: 16px;
  margin: 0px;

  li {
    margin-bottom: 4px;

    &:last-child {
      margin-bottom: 0px;
    }
  }
}
</style>
