<template>
  <Confirm
    :value="value"
    icon="eva-info-outline"
    icon-color="orange"
    @input="hideSheet"
  >
    <template #title>Default view?</template>

    <template #default>
      <div class="q-py-md">
        <p>
          Do you want to make this <b>"({{ view }})"</b> view is default? .
        </p>

        <p>You can always change this view.</p>
      </div>
    </template>

    <template #footer>
      <BaseButton
        is-flat
        label="no"
        class="q-mr-sm"
        color="orange"
        @click="handleNo"
      />
      <BaseButton label="yes" color="orange" @click="handleYes" />
    </template>
  </Confirm>
</template>

<script>
import Confirm from "@/components/common/popup/Confirm.vue";

export default {
  name: "DefaultView",

  components: { Confirm },

  props: {
    value: {
      type: Boolean,
      required: true,
    },

    view: {
      type: String,
      required: true,
    },
  },

  methods: {
    handleNo() {
      this.$emit("handleNo", false);
    },

    hideSheet() {
      this.$emit("hideSheet", false);
    },

    handleYes() {
      this.$emit("handleYes", false);
    },
  },
};
</script>

<style lang="scss" scoped></style>
